:root {
  --primary: #d4d3ff;
  --background: #e6e6e6;
  --footer-bg: #4b99ff00;
  --text: #222;
  --accent: #343438;
  --card-bg: #fff;
  --card-shadow: 0 2px 8px rgba(0,0,0,0.08);
  --theme-toggle-text: #fff;
}

.app-container.dark {
  --background: #222831;
  --footer-bg: #22283100;
  --text: #EEEEEE;
  --card-bg: #31363F;
  --primary: #76ABAE;
  --accent: #EEEEEE;
  --theme-toggle-text: #222;
  background: linear-gradient(135deg, #23282d 0%, #0444ab 100%);
}

body, .app-container {
  min-height: 100vh;
  margin: 0;
  font-family: 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(135deg, #fff 0%, #bad3ff 100%);
  color: var(--text);
  line-height: 1.7;
}

body, .app-container,
.navbar,
.theme-toggle-floating,
.nav-tabs li button,
.tab-content,
footer,
.accordion-content,
.course-details {
  transition: background 0.4s cubic-bezier(0.4, 0, 0.2, 1), color 0.4s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.4s;
}

header {
  width: 100%;
}

.navbar {
  position: fixed; /* changed from absolute to fixed */
  left: 50%;
  top: 1.5rem;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.15); /* more transparent for glass effect */
  border-radius: 999px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  padding: 0.5rem 2.5rem;
  min-width: 320px;
  max-width: 90vw;
  width: auto;
  z-index: 100;
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}

/* Center logo and nav-tabs inside bubble */
.logo {
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 1px;
  color: var(--accent);
  margin-right: 2rem;
}

.nav-tabs {
  display: flex;
  gap: 1.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: center;
}

.nav-tabs li button {
  background: none;
  border: none;
  font-size: 1rem;
  padding: 0.7rem 1.5rem;
  cursor: pointer;
  color: var(--text);
  border-radius: 999px; /* fully rounded bubble */
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  margin: 0 0.2rem;
  box-shadow: none;
}

.nav-tabs li button.active,
.nav-tabs li button:hover {
  background: linear-gradient(90deg, #bad3ff 60%, #d4d3ff 100%);
  color: #343438;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(186, 211, 255, 0.18);
  border-radius: 999px;
}

.theme-toggle-floating {
  position: fixed; /* changed from absolute to fixed */
  top: 1.5rem;
  right: calc(5vw + 2rem);
  background: rgba(255, 255, 255, 0.15); /* more transparent for glass effect */
  border-radius: 50%;
  width: 56px;
  height: 56px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 101;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(12px);
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
  border: none !important;
  outline: none !important;
  transform-origin: center;
  -webkit-appearance: none;
  overflow: hidden;
}

.theme-toggle-floating:hover {
  background: linear-gradient(135deg, #bad3ff 60%, #d4d3ff 100%);
  transform: scale(1.05);
  box-shadow: 0 4px 16px rgba(186, 211, 255, 0.25);
}

.app-container.dark .theme-toggle-floating:hover {
  background: linear-gradient(135deg, #233864 60%, #2f2f65 100%);
  box-shadow: 0 4px 16px rgba(40, 44, 52, 0.25);
  transform: scale(1.05);
}

.theme-toggle-icon {
  font-size: 1.8rem;
  color: #343438;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  transform-origin: center;
  border: none !important;
  outline: none !important;
  background: transparent !important;
}

/* Different margins for dark mode (sun icon) */
.app-container.dark .theme-toggle-icon {
  transform: rotate(180deg);
}

.theme-toggle-floating,
.theme-toggle-floating:focus,
.theme-toggle-floating:active,
.theme-toggle-icon,
.theme-toggle-icon:focus,
.theme-toggle-icon:active {
  border: none !important;
  outline: none !important;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
  -webkit-tap-highlight-color: transparent;
}

.theme-toggle-floating * {
  border: none !important;
  outline: none !important;
  background: transparent !important;
}

.theme-toggle-floating *,
.theme-toggle-floating button,
.theme-toggle-floating svg {
  border: none !important;
  outline: none !important;
  box-shadow: none !important;
  background: transparent !important;
  border-radius: 50%;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
}

.theme-toggle-floating::after,
.theme-toggle-floating::before {
  display: none !important;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1;
  width: 100%;
  max-width: 75vw;
  margin: 6rem auto 2rem auto; /* increased top margin to clear navbar */
  padding: 1rem;
  box-sizing: border-box;
}

.fade-in {
  animation: fadeIn 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(16px);}
  to { opacity: 1; transform: translateY(0);}
}

footer {
  background: var(--footer-bg);
  width: 100%;
  padding: 1rem 0 1rem 0;
  text-align: center;
  color: var(--text);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

footer h1 {
  margin: 0.5rem 0;
  font-size: 1.2rem;
}

footer p {
  margin: 0.2rem 0 1rem 0;
}

.icon-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
}

.icon {
  padding: 0.5rem;
  border-radius: 50%;
  transition: background 0.2s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.icon.github {
  color: #333;
}

.icon.linkedin {
  color: #0077b5;
}

.icon.github:hover {
  background: #333;
  color: #fff;
}

.icon.linkedin:hover {
  background: #0077b5;
  color: #fff;
}

small {
  margin-top: 0.5rem;
  color: #888;
}

.tab-content h1 {
  font-size: 2.25rem;
  margin-bottom: 0.5rem;
  color: var(--accent);
}

.tab-content h2 {
  margin-top: 1.75rem;
  color: var(--accent);
  font-size: 2rem;
}

.accordion {
  margin-top: 1rem;
}

.accordion-item {
  margin-bottom: 0.5rem;
  border-radius: 8px;
  background: var(--card-bg);
  box-shadow: var(--card-shadow);
  overflow: hidden;
}

.accordion-title {
  width: 100%;
  text-align: left;
  background: none;
  border: none;
  padding: 1rem;
  font-size: 1.1rem;
  cursor: pointer;
  color: var(--accent);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}

.accordion-content {
  padding: 1rem;
  background: var(--background);
  color: var(--text);
  border-top: 1px solid #eee;
}

.course-list {
  margin-top: 1rem;
}

.course-card {
  margin-bottom: 1rem;
  border-radius: 8px;
  background: var(--card-bg);
  box-shadow: var(--card-shadow);
  overflow: hidden;
}

.course-title {
  width: 100%;
  text-align: left;
  background: none;
  border: none;
  padding: 1rem;
  font-size: 1.1rem;
  cursor: pointer;
  color: var(--accent);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.course-code {
  font-weight: bold;
  color: var(--accent);
}

.accordion-content,
.course-details {
  padding: 0;
  background: var(--card-bg);
  color: var(--text);
  border-top: none;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 1s cubic-bezier(0.25, 1, 0.25, 1), opacity 0.6s, padding 0.3s;
}

.accordion-content.open,
.course-details.open {
  max-height: 500px;
  opacity: 1;
  overflow: visible;
  padding: 1rem;
  border-top: 1px solid #eee;
}

.arrow {
  font-size: 1rem;
  margin-left: 0.5rem;
}

.app-container.dark .accordion-content,
.app-container.dark .course-details {
  background: var(--card-bg);
  color: var(--text);
  border-top: 1px solid #444;
}

.app-container.dark a:not(.icon) {
  color: #76ABAE;
}

.app-container.dark .icon.github {
  color: #EEEEEE;
}

.app-container.dark .navbar,
.app-container.dark .theme-toggle-floating {
  background: rgba(40, 44, 52, 0.25);
  border-color: rgba(80, 80, 80, 0.2);
}

.course-meta-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.course-br {
  margin-left: 2rem;
  color: var(--accent);
}

.app-container.dark .accordion-content.open,
.app-container.dark .course-details.open {
  border-top: 1px solid #444;
}

/* --- Timeline Styles --- */
.timeline-row {
  display: flex;
  align-items: flex-start;
  position: relative;
  margin-bottom: 2.5rem;
}

.timeline-card {
  position: relative;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.07);
  padding: 1.5rem 2rem;
  min-width: 320px;
  flex: 1;
  overflow: hidden;
  min-height: 80px;
}

.timeline-card {
  transition:
    background 0.2s,
    box-shadow 0.2s,
    transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}


/* Faded background image for each country */
.timeline-card.timeline-bg-canada::before {
  background-image: url('./images/canada.jpg');
}
.timeline-card.timeline-bg-hk::before {
  background-image: url('./images/hongkong.JPG');
}
.timeline-card.timeline-bg-taiwan::before {
  background-image: url('./images/taiwan.JPG');
}

.timeline-card.timeline-bg-canada::before,
.timeline-card.timeline-bg-hk::before,
.timeline-card.timeline-bg-taiwan::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 0;
  opacity: 0.10;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 18px;
  pointer-events: none;
  transition:
    opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1),
    background-size 1s cubic-bezier(0.4, 0, 0.2, 1),
    background-position 1s cubic-bezier(0.4, 0, 0.2, 1),
    border-radius 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.app-container.dark .timeline-card.timeline-bg-canada::before,
.app-container.dark .timeline-card.timeline-bg-hk::before,
.app-container.dark .timeline-card.timeline-bg-taiwan::before {
  opacity: 0.22; /* Example: higher opacity for dark mode */
}

/* Ensure content is above the faded image */
.timeline-card > * {
  position: relative;
  z-index: 1;
}

/* Timeline card content layout */
.timeline-card-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: calc(100% - 150px); /* Make space for the year badge */
  gap: 0.3rem;
  margin-bottom: 1rem;
}
.timeline-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #222;
  margin-bottom: 0.2rem;
}
.timeline-subtitle {
  color: #222;
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
}
.timeline-details {
  margin: 0;
  padding-left: 1.1em;
  color: #666;
  font-size: 1rem;
}

/* Absolutely position the year badge at top right */
/* Adjust years badge to include dropdown button */
.timeline-years {
  position: absolute;
  top: 1.2rem;
  right: 3.5rem; /* Make room for dropdown button */
  background: #f5f5f500;
  color: #222;
  font-weight: bold;
  border-radius: 999px;
  padding: 0.4em 1.2em;
  font-size: 1.1rem;
  white-space: nowrap;
  z-index: 5;
}


/* Gray mask for timeline card background images in dark mode */
.app-container.dark .timeline-card::after {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 0;
  background: rgba(9, 29, 74, 0.772); /* adjust opacity as needed */
  border-radius: 18px;
  pointer-events: none;
}

.app-container.dark .timeline-card .timeline-title,
.app-container.dark .timeline-card .timeline-subtitle {
  color: #fff;
}

.app-container.dark .timeline-card .timeline-years {
  color: #fff;
  background: #7b7f8400;
}

.app-container.dark .timeline-card .timeline-details {
  color: #e7edef;
}

.icon.email {
  color: #555;
}

.icon.email:hover {
  background: #555;
  color: #fff;
}

.app-container.dark .icon.email {
  color: #EEEEEE;
}

.app-container.dark .icon.email:hover {
  background: #EEEEEE;
  color: #222;
}

/* If you want a tooltip showing the email on hover */
.icon.email {
  position: relative;
}

.icon.email:hover::after {
  content: "cheunghsinkuang@gmail.com";
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0,0,0,0.8);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.75rem;
  white-space: nowrap;
}

.app-container.dark .nav-tabs li button.active,
.app-container.dark .nav-tabs li button:hover {
  background: linear-gradient(90deg, #233864 60%, #2f2f65 100%);
  color: #fff;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(40, 44, 52, 0.18);
  border-radius: 999px;
}

.app-container.dark .theme-toggle-icon .sun {
  color: #fff !important;
}

.timeline-details-dropdown {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 1s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.6s, padding 1s cubic-bezier(0.4, 0, 0.2, 1), visibility 0s 1s;
  visibility: hidden;
}

.timeline-details-dropdown.open {
  max-height: 500px;
  opacity: 1;
  visibility: visible;
  transition: max-height 1s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.6s, padding 1s cubic-bezier(0.4, 0, 0.2, 1);
}

.timeline-dropdown-btn {
  position: absolute;
  top: 1.75rem;
  right: 1rem;
  background: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 4px;
  color: var(--accent);
  font-size: 1rem;
  cursor: pointer;
  transition: color 0.2s, background 0.2s;
  padding: 0.3rem 0.5rem;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
}

.timeline-dropdown-btn:hover {
  background: rgba(0, 0, 0, 0.1);
}

.app-container.dark .timeline-dropdown-btn {
  color: #ffffff;
}

.app-container.dark .timeline-dropdown-btn:hover {
  background: rgba(255, 255, 255, 0.35);
}

/* Ensure card, image, and mask all use the same border-radius and are perfectly aligned */
.timeline-card,
.timeline-card::before,
.timeline-card::after {
  border-radius: 18px;
  overflow: hidden;
}

/* Optional: Make card background fully opaque */
.timeline-card {
  background: #fff;
}

/* If you still see lines, try adding a 1px solid border that matches the card background */
.timeline-card {
  border: 1px solid #fff;
}

/* In dark mode, match the border to the card background */
.app-container.dark .timeline-card {
  background: #31363F;
  border: 1px solid #31363F;
}

/* Dark mode: match timeline card background and border */
.app-container.dark .course-card {
  background: #162a4f;
}

.app-container.dark .course-details {
  background: #162a4f;
  color: #e7edef;
}

/* Scale up and highlight dropdown buttons on hover */
.timeline-dropdown-btn {
  transition: 
    background 0.2s,
    color 0.2s,
    transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.timeline-dropdown-btn:hover {
  background: rgba(0, 0, 0, 0.12);
  transform: scale(1.25);
}

/* Dark mode: scale and highlight for dropdown buttons */
.app-container.dark .timeline-dropdown-btn:hover {
  background: rgba(129, 169, 255, 0.35);
  color: #000;
  transform: scale(1.25);
}

/* Highlight and scale up the entire course card on hover */
.course-card {
  transition:
    background 0.2s,
    box-shadow 0.2s,
    transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.course-card:hover {
  background: #bad3ff;
  box-shadow: 0 8px 32px rgba(186, 211, 255, 0.18);
  transform: scale(1.03);
}

/* Dark mode: highlight and scale for course card */
.app-container.dark .course-card:hover {
  background: #233864;
  box-shadow: 0 8px 32px rgba(40, 44, 52, 0.18);
}

@media (min-width: 901px) {
  .icon:hover {
    transform: scale(1.18);
    box-shadow: 0 4px 16px rgba(186, 211, 255, 0.18);
    transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s;
    z-index: 2;
  }
}

/* Hamburger Menu Styles */
.hamburger-menu {
  display: none; /* Hidden by default on desktop */
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 102;
  margin-right: 1rem;
}

.hamburger-menu span {
  display: block;
  width: 100%;
  height: 3px;
  background: var(--accent);
  border-radius: 3px;
  transition: all 0.3s;
}

.app-container.dark .hamburger-menu span {
  background: #fff;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  background: var(--card-bg);
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  border-radius: 8px;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: max-height 0.3s ease, opacity 0.3s ease;
  z-index: 101;
}

/* Make the dropdown menu visible when open */
.dropdown-menu.open {
  display: block !important;
  max-height: 300px !important;
  opacity: 1 !important;
  padding: 8px 0 !important; /* Remove padding to allow full-width highlighting */
  overflow: hidden !important;
  border-radius: 8px !important;
  width: 100px;
  min-width: 100px;
  max-width:100px;
}

.mobile-tabs {
  display: flex;
  flex-direction: column;
  padding: 0;
  width: 100%;
  margin: 0;
}

.mobile-tabs li {
  width: 100%;
  margin: 0;
  padding: 0;
}


.mobile-tabs li button {
  width: 100%;
  text-align: left;
  padding: 12px 16px;
  border-radius: 0 !important; /* No rounded corners */
  margin: 0 !important;
  transition: none; /* Remove transitions */
  border: none;
  background: transparent;
  color: var(--text);
  font-size: 16px;
}

/* Simple active tab highlight with no gradient */
.mobile-tabs li button.active,
.app-container.dark .mobile-tabs li button.active {
  background-color: #bad3ff !important;
  background-image: none !important; /* Remove any gradient */
  color: #343438 !important;
  font-weight: bold !important;
  box-shadow: none !important;
  border-radius: 0 !important;
  width: 100% !important;
  display: block !important;
  text-align: left !important;
}

.app-container.dark .mobile-tabs li button.active {
  background-color: #233864 !important;
  background-image: none !important;
  color: #ffffff !important; /* Ensure text is white */
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3) !important; /* Add shadow for better contrast */
  font-weight: bold !important;
  border-radius: 0 !important;
}

/* Remove hover effects completely */
.mobile-tabs li button:hover {
  background: transparent !important;
}

/* Remove all the overly specific selectors that might be causing conflicts */
html body .dropdown-menu .mobile-tabs li button:hover,
html body .navbar .dropdown-menu .mobile-tabs li button:hover,
html body .app-container.dark .dropdown-menu .mobile-tabs li button:hover,
html body .app-container.dark .navbar .dropdown-menu .mobile-tabs li button:hover {
  background: transparent !important;
}

html body .dropdown-menu .mobile-tabs li button.active,
html body .app-container.dark .dropdown-menu .mobile-tabs li button.active {
  border-radius: 0 !important;
  width: 100% !important;
  box-shadow: none !important;
  background-image: none !important;
}

html body .app-container.dark .dropdown-menu .mobile-tabs li button.active,
html body .app-container.dark .navbar .dropdown-menu .mobile-tabs li button.active {
  color: #ffffff !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3) !important;
}

/* Media query to show hamburger on mobile */
@media (max-width: 460px) {
  .navbar .nav-tabs.desktop-tabs,
  header .nav-tabs.desktop-tabs,
  .app-container .nav-tabs.desktop-tabs {
    display: none !important; /* Force it to be hidden */
  }

  .dropdown-menu {
    top: 45px; /* Position right below the hamburger button */
    width: 200px;
    left: 0;
    right: auto; /* Don't stretch to right edge */
  }
  
  .hamburger-menu {
    margin: 0 !important;
    display: flex;
    align-items: center;
    height: 21px;
    width: 30px;
  }

  /* Animate hamburger to X when menu is open */
  .hamburger-menu.open span:nth-child(1) {
    transform: translateY(9px) rotate(45deg);
  }
  
  .hamburger-menu.open span:nth-child(2) {
    opacity: 0;
  }
  
