}

.song-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  width: 100%;
  align-items: start;
  grid-auto-rows: min-content;
}

.song-card {
  background: var(--card-bg);
  border-radius: 18px;
  box-shadow: var(--card-shadow);
  overflow: hidden;
  transition: 
    background 0.2s,
    box-shadow 0.2s,
    transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  height: auto;
  display: flex;
  flex-direction: column;
  align-self: start;
